body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  font-family: 'Lato', sans-serif;
  margin: 0;
  padding: 0;
  background-image: url('image.png');
  background-repeat: repeat;
  color: white;
  min-height: 100vh; /* Ensure the minimum height is full viewport */
}

header , footer {
  background-color: rgba(255, 255, 255, 0.02); /* More transparent for glass effect */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2); /* Deeper shadow for depth */
  backdrop-filter: blur(20px); /* Increased blur for a more frosted look */
  -webkit-backdrop-filter: blur(20px); /* For Safari compatibility */
  color: white;
  padding: 20px 0;
  text-align: center;
}

.header-content {
  opacity: 1 !important;
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

nav ul {
  list-style: none;
  padding: 0;
  display: flex;
}

nav ul li {
  margin: 0 10px;
}

nav ul li a {
  color: white;
  text-decoration: none;
  transition: color 0.3s ease;
}

nav ul li a:hover {
  color: #FFD600; /* Highlight color on hover */
}

.services {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  padding: 50px 5%; /* Adjusted padding for responsiveness */
  text-align: center;
}

.service {
  width: calc(33% - 20px); /* Adjust width for spacing */
  margin: 10px;
  padding: 20px;
  background-color: rgba(255, 255, 255, 0.1); /* Light background for service cards */
  border-radius: 10px;
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  color: white;
  transition: transform 0.3s ease;
}

.service:hover {
  transform: translateY(-5px); /* Slight raise effect on hover */
}

.service h2 {
  color: #FFD600; /* Title color */
}

.price {
  color: #4CAF50; /* Price color */
  font-weight: bold;
  font-size: 1.2em;
  margin-top: 10px; /* Space out the price from the description */
}

footer {
  background-color: rgba(255, 255, 255, 0.02); /* Match the header's transparency */
  box-shadow: 0 -4px 8px rgba(0, 0, 0, 0.2); /* Shadow inside the footer */
  padding: 10px 0;
  position: absolute;
  bottom: 0;
  width: 100%;
}

footer p {
  margin: 0; /* Removes default margin from p tag */
}

